color: #51cf66;\r\n  --danger-color: #ff6b6b;\r\n  --warning-color: #ffd43b;\r\n  --info-color: #74c0fc;\r\n  --light-color: #2d3748;\r\n  --dark-color: #1a202c;\r\n  --border-color: #4a5568;\r\n  --shadow: 0 2px 4px rgba(0,0,0,0.3);\r\n  --shadow-hover: 0 4px 8px rgba(0,0,0,0.4);\r\n}\r\n\r\n/* Reset and Base Styles */\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\r\n    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\r\n    sans-serif;\r\n  -webkit-font-smoothing: antialiased;\r\n  -moz-osx-font-smoothing: grayscale;\r\n  background-color: var(--light-color);\r\n  color: var(--dark-color);\r\n  line-height: 1.6;\r\n  transition: var(--transition);\r\n}\r\n\r\n.app-container {\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* Header Styles */\r\n.app-header {\r\n  background: linear-gradient(135deg, var(--primary-color), #0056b3);\r\n  color: white;\r\n  padding: 1rem 2rem;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  box-shadow: var(--shadow);\r\n  position: sticky;\r\n  top: 0;\r\n  z-index: 1000;\r\n}\r\n\r\n.header-left {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.header-left h1 {\r\n  font-size: 1.5rem;\r\n  font-weight: 600;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.version {\r\n  background: rgba(255, 255, 255, 0.2);\r\n  padding: 0.25rem 0.5rem;\r\n  border-radius: 12px;\r\n  font-size: 0.75rem;\r\n  font-weight: 500;\r\n}\r\n\r\n.header-right {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.theme-toggle {\r\n  background: rgba(255, 255, 255, 0.2);\r\n  border: none;\r\n  color: white;\r\n  padding: 0.5rem;\r\n  border-radius: 50%;\r\n  cursor: pointer;\r\n  font-size: 1rem;\r\n  transition: var(--transition);\r\n}\r\n\r\n.theme-toggle:hover {\r\n  background: rgba(255, 255, 255, 0.3);\r\n  transform: scale(1.1);\r\n}\r\n\r\n.status-indicator {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n  font-size: 0.9rem;\r\n}\r\n\r\n.status-dot {\r\n  width: 8px;\r\n  height: 8px;\r\n  border-radius: 50%;\r\n  background: var(--success-color);\r\n}\r\n\r\n.status-dot.loading {\r\n  background: var(--warning-color);\r\n  animation: pulse 1s infinite;\r\n}\r\n\r\n@keyframes pulse {\r\n  0%, 100% { opacity: 1; }\r\n  50% { opacity: 0.5; }\r\n}\r\n\r\n/* Notifications */\r\n.notifications-container {\r\n  position: fixed;\r\n  top: 80px;\r\n  right: 20px;\r\n  z-index: 1001;\r\n  max-width: 400px;\r\n}\r\n\r\n.notification {\r\n  background: white;\r\n  border-left: 4px solid var(--info-color);\r\n  padding: 1rem;\r\n  margin-bottom: 0.5rem;\r\n  border-radius: var(--border-radius);\r\n  box-shadow: var(--shadow);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  animation: slideIn 0.3s ease-out;\r\n}\r\n\r\n.notification.success {\r\n  border-left-color: var(--success-color);\r\n}\r\n\r\n.notification.error {\r\n  border-left-color: var(--danger-color);\r\n}\r\n\r\n.notification.warning {\r\n  border-left-color: var(--warning-color);\r\n}\r\n\r\n.notification button {\r\n  background: none;\r\n  border: none;\r\n  font-size: 1.2rem;\r\n  cursor: pointer;\r\n  padding: 0;\r\n  margin-left: 1rem;\r\n  color: var(--secondary-color);\r\n}\r\n\r\n@keyframes slideIn {\r\n  from {\r\n    transform: translateX(100%);\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    transform: translateX(0);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n/* Quick Stats */\r\n.quick-stats {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\r\n  gap: 1rem;\r\n  padding: 1rem 2rem;\r\n  background: white;\r\n  border-bottom: 1px solid var(--border-color);\r\n}\r\n\r\n[data-theme=\"dark\"] .quick-stats {\r\n  background: var(--dark-color);\r\n}\r\n\r\n.stat-card {\r